* {
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  font-size: 2.5vh;
}

body {
  background-image: url("bg_login.f215f3a6.webp");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100vh;
  margin: 0;
  padding: 0;
}

#grad {
  background-image: linear-gradient(to right, #f000, red);
  height: 200px;
}

.logo {
  object-fit: fill;
  width: auto;
  height: auto;
  max-height: 200px;
}

.logo-container {
  justify-content: center;
  display: flex;
}

.wrapper {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
}

form {
  border: 3px solid #f8f7f3;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  width: 25vw;
  height: auto;
  box-shadow: 0 5px 10px #00000040;
}

.login_container {
  background-color: #f8f7f3;
  flex-direction: column;
  padding: 10px;
  display: flex;
}

input[type="text"], input[type="password"] {
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 12px 20px;
  display: inline-block;
}

label[for="email"] {
  grid-area: 1 / 1;
  align-items: center;
  margin-top: 15px;
  margin-bottom: 10px;
  margin-left: 10px;
}

label[for="password"] {
  grid-area: 3 / 1;
  margin: 10px;
}

input[type="text"] {
  grid-area: 2 / 1 / auto / -1;
}

input[type="password"] {
  grid-area: 4 / 1 / auto / -1;
}

button {
  color: #fff;
  cursor: pointer;
  text-align: center;
  box-sizing: border-box;
  background-color: #c43e4a;
  border: none;
  border-radius: 10px;
  width: 100%;
  margin: 10px 0;
  padding: 14px 20px;
  text-decoration: none;
  display: block;
}

input, a {
  width: 100%;
}

.forgot {
  grid-area: 6 / 1;
  margin: 10px;
}

.register {
  text-align: right;
  grid-area: 6 / 2;
  margin: 10px;
}

button:hover {
  opacity: .8;
}
